import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/eventCenter/api";

@Component({})
export default class EventRecordList extends Vue {
  // 表格信息
  tableData: any = [];
  loading: any = false;
  // 分页信息
  pagination: any = {
    current: 1,
    size: 10,
    total: 0,
    query: {
      name: ""
    }
  };
  // 复选框选中行数据
  multipleSelection: Array<any> = [];
  //筛选组织
  filterOrgId: any = "";
  // 通用适配prop
  typeDetailProps: any = {
    label: "name",
    value: "id",
    checkStrictly: true
  };

  created() {
    this.getList(); //获取表格
  }
  // 切换到详情页面（添加和编辑）
  // @Emit("switch")
  switchToDetails(item: any) {
    console.log(item);
    this.$router.push({
      name: "eventRecordDetail",
      params: {
        item
      }
    });
  }
  // @Watch("filterOrgId")
  // WatchFilterOrg(newParam: any, oldParam: any) {
  //   this.pagination.query.organizationId = Utils.lastOfArr(this.filterOrgId);
  // }
  // 根据关键字，每页查询数量，当前页获得所有列表
  getList() {
    this.loading = true;
    this.$http.post(api.getEventRecord, this.pagination).then((res: any) => {
      this.loading = false;
      if (res.status) {
        if (res.data.status) {
          this.tableData = res.data.data.records;
          this.pagination.total = res.data.data.total;
        } else {
          this.$message.error(res.data.message);
        }
      }
    });
  }

  // 获得所有复选框选中数据
  getAllSelected(val: Array<any>) {
    this.multipleSelection = val;
  }
}
